import { Node, Block } from '@ali/4ever-cangjie';
import { JsonML } from '@ali/4ever-utils';
import { typeToTag } from './constants';

export default {
  match: (node: Node): boolean =>{
    return Block.isBlock(node) && !!typeToTag[node.type];
  },

  convert: (node: Block): JsonML => {
    // @ts-ignore
    return [typeToTag[node.type], node.data];
  },
};
